import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuCellGroup from '@/components/custom/cu-cell-group.vue'
import simpleAddress from '@/components/simple-address/simple-address.nvue'
import { create } from '@/api/common.js'
import { api } from '@/config/common.js'
import getGlobalData from '@/utils/business.js'
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup,
		simpleAddress
	},
	data() {
		return {
			title: '添加客户&供应商',
			reqData: {
				contactunitname: '',
				contactunittype: 3,
				querycode: '',
				province: 0,
				city: '',
				district: '',
				address: '',
				bseContactUnitContactModels:[
					{
						contactname: '',
						telephone: '',
						email: ''
					}
				],
				remarks: ''
			},
			disableSubmit: true,
			addressArray: [0, 0, 0]
		}
	},
	onLoad() {
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		},
		mainHeight() {
			return this.$mainHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleTypeChange(val) {
			this.reqData.contactunittype = val.detail.value
		},
		handleOpenAddress() {
			this.$refs.address.open()
		},
		handleAddressChange(val) {
			this.addressArray = []
			for (let item of val.label.split('-')) {
				this.addressArray.push(item)
			}
			this.reqData.province = this.addressArray[0]
			this.reqData.city = this.addressArray[1]
			this.reqData.district = this.addressArray[2]
		},
		handleSubmit() {
			create(api.contactUnit, {model: this.reqData }).then(res => {
				if (res.status == 200 && res.data.returnCode == '0000') {
					getGlobalData.getCurrentUnit().then(res => {
						uni.navigateBack({
							delta: 1
						})
					}).catch(err => {
						uni.navigateBack({
							delta: 1
						})
					})
				} else {
					uni.showToast({
						title: '添加失败'
					})
				}
			}).catch(error => {
				uni.showToast({
					title: '添加失败'
				})
			})
		}
	},
	watch:{
		reqData: {
			handler(val) {
				if (val.contactunitname) {
					this.disableSubmit = false
				} else {
					this.disableSubmit = true
				}
			},
			deep: true
		}
	}
}
